'use client';

import { useState, useEffect } from 'react';
import { useQuery } from '@tanstack/react-query';
import { fetchLeadByContactId } from '@/lib/api';
import { ChatwootContact } from '@/lib/types';
import LeadDetails from '@/components/LeadDetails';
import LeadPipeline from '@/components/LeadPipeline';
import ActionsPanel from '@/components/ActionsPanel';
import CreateLeadModal from '@/components/CreateLeadModal';
import AIAssistant from '@/components/AIAssistant';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Loader2, AlertCircle } from 'lucide-react';
import { Button } from '@/components/ui/button';

export default function Dashboard() {
  const [contact, setContact] = useState<ChatwootContact | null>(null);
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);

  // Listen to Chatwoot events
  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      if (typeof event.data !== 'string') return;

      try {
        const parsed = JSON.parse(event.data);
        if (parsed.event !== 'appContext') return;

        const contactData = parsed.data.contact;
        if (contactData) {
          setContact({
            id: contactData.id,
            name: contactData.name,
            email: contactData.email,
            phone_number: contactData.phone_number,
            custom_attributes: contactData.custom_attributes,
          });
        }
      } catch (e) {
        console.error("Error parsing Chatwoot event", e);
      }
    };

    window.addEventListener('message', handleMessage);
    // Request context
    window.parent.postMessage('chatwoot-dashboard-app:fetch-info', '*');

    return () => window.removeEventListener('message', handleMessage);
  }, []);

  // Fetch Odoo Lead
  const { data, isLoading, error, refetch } = useQuery({
    queryKey: ['lead', contact?.id],
    queryFn: () => fetchLeadByContactId(contact!.id),
    enabled: !!contact?.id,
  });

  if (!contact) {
    return (
      <div className="flex h-screen items-center justify-center p-4">
        <Card className="w-full max-w-md">
          <CardContent className="flex flex-col items-center justify-center p-6 text-center">
            <Loader2 className="mb-4 h-8 w-8 animate-spin text-muted-foreground" />
            <p className="text-muted-foreground">Wachten op Chatwoot data...</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <main className="container mx-auto max-w-3xl p-4 space-y-6 pb-20">
      <div className="flex items-center justify-between">
        <h1 className="text-xl font-bold">Sales Command Center</h1>
        <span className="text-xs text-muted-foreground">Chatwoot ID: {contact.id}</span>
      </div>

      {isLoading && (
        <div className="flex justify-center p-8">
          <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
        </div>
      )}

      {error && (
        <Card className="border-red-200 bg-red-50">
          <CardContent className="flex items-center p-4 text-red-800">
            <AlertCircle className="mr-2 h-5 w-5" />
            Fout bij laden van Odoo data.
          </CardContent>
        </Card>
      )}

      {!isLoading && !error && !data?.lead && (
        <Card>
          <CardContent className="flex flex-col items-center justify-center p-8 space-y-4">
            <div className="text-lg font-semibold">Geen Odoo lead gevonden</div>
            <p className="text-center text-muted-foreground">
              Dit contact is nog niet gekoppeld aan een Odoo lead.
            </p>
            <Button onClick={() => setIsCreateModalOpen(true)}>
              Maak of Koppel Lead
            </Button>
          </CardContent>
        </Card>
      )}

      {!isLoading && data?.lead && (
        <>
          <Card>
             <CardHeader className="pb-2">
                <div className="flex justify-between items-center">
                    <CardTitle>{data.lead.name}</CardTitle>
                    <span className="text-xs text-muted-foreground">ID: {data.lead.id}</span>
                </div>
             </CardHeader>
             <CardContent>
                <LeadPipeline lead={data.lead} />
             </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="text-base">Lead Details</CardTitle>
            </CardHeader>
            <CardContent>
              <LeadDetails lead={data.lead} />
            </CardContent>
          </Card>

          <Card>
             <CardContent className="pt-6">
                <ActionsPanel lead={data.lead} />
             </CardContent>
          </Card>
        </>
      )}

      <CreateLeadModal 
        isOpen={isCreateModalOpen} 
        onClose={() => {
            setIsCreateModalOpen(false);
            refetch();
        }}
        contact={contact} 
      />

      <AIAssistant />
    </main>
  );
}
